@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.table {
  margin-left: auto;
  margin-right: auto;
}

hr {
  background-color: #e4e4e4;
}

.overflow-auto {
  overflow: auto;
}

.diversity:before {
  background-image: url(/upa-ic/static/media/woman-panel.fe7c6a06.jpeg) !important;
}

.hero {
  position: relative;
}

.hero::before {
  background-image: url(/upa-ic/static/media/banner.983f56a4.jpeg);
  background-size: cover;
  background-position: center;
  -webkit-filter: blur(3px) brightness(0.35);
          filter: blur(3px) brightness(0.35);
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.slide {
  display: flex;
  height: 80%;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

.slider-navigation-previous,
.slider-navigation-next {
  display: none;
}

.slide-text {
  max-width: 800px;
  display: block;
  margin: auto;
  font-style: italic;
}

.img {
  display: block;
  padding: 0 0 5px 5px;
  width: 128px;
}

.modal-card-title {
  flex-shrink: 1;
}

@media screen and (max-width: 769px), print {
  .column.is-narrow .img {
      margin: auto;
  }
}
.schedule-item {
  cursor: pointer;
}

.schedule-item:hover {
  background-color: #d4e9f5 !important;
}

.sponsor-container {
  padding: 2em;
}

.sponsor {
  max-width: 128px;
  height: auto;
}

.navbar {
  border-bottom: 1px solid #ccc;
}

.navbar-item.is-active {
}

.navbar-item.is-active:nth-child(1) {
  background-color: #f7e6d0 !important;
}
.navbar-item.is-active:nth-child(2) {
  background-color: #f7d0db !important;
}
.navbar-item.is-active:nth-child(3) {
  background-color: #d0eaf7 !important;
}

